{\n        justify-content: center;\n    }\n\n    .logo-container i,\n    .logo-container h1 {\n        font-size: 3rem;\n    }\n\n    .navList {\n        flex-direction: column;\n        position: absolute;\n        transform: translateY(-100%);\n        transition: transform 0.2s;\n        top: 0;\n        right: 0;\n        z-index: 99;\n        width: 100vw;\n        text-align: center;\n        background-color: rgba(33,82,78, 0.9);\n        color: whitesmoke;\n    }  \n\n    .showMenu {\n        transform: translateY(0);\n    }\n\n    .hamburger {\n        font-size: 1.8rem;\n        position: absolute;\n        z-index: 100;\n        top: 1rem;\n        right: 2rem;\n        cursor: pointer;\n        display: block;\n    }\n\n    .hamburger #close-btn {\n        display: none;\n    }\n\n    .hamburger #openMenu-btn {\n        display: block;\n    }\n\n    .navbar a:hover::after {\n        width: 100%;\n        background-color: whitesmoke;\n    }\n}\n\n.navbar a::after {\n    content: '';\n    display: block;\n    width: 0;\n    height: 3px;\n    background-color: #317b76;\n    transition: width .3s;\n}\n\n.navbar a:hover::after {\n    width: 100%;\n}\n\n.main-content {\n    padding: 1rem;\n}\n\n/*************************/\n/* Hero section Styling */\n/************************/\n\n.hero {\n    display: flex;\n    justify-content: space-evenly;\n    align-items: center;\n    animation: fadeIn 500ms ease-in;\n}\n\n.hero .left-container {\n    display: flex; \n    flex-direction: column;\n    width: 500px;\n    gap: .5rem;\n    align-items: center;\n    padding: 1.3rem;\n    font-size: 1.2rem;\n}\n\n.left-container h1 {\n    padding: 0.5rem;\n    font-size: 2.5rem;\n}\n\n.left-container span {\n    margin-left: 1rem;\n}\n\n.left-container button {\n    padding: .7rem;\n    border: none;\n    font-size: 1.1rem;\n    font-weight: bold;\n    margin-top: 1rem;\n    background-color: #317b76;\n    color: inherit;\n    border-radius: 5px;\n}\n\n.left-container button:hover {\n    background-color: #21524e;\n    cursor: pointer;\n}\n\n.right-container {\n    padding: 1.3rem;\n    position: relative;\n}\n\n.right-container div {\n    width: 300px;\n    height: 450px;\n    border: 3px solid #317b76;\n    position: absolute;\n    transform: rotate(350deg);\n}\n\n.right-container img {\n    width: 300px;\n    transform: rotate(8deg);\n    object-fit: cover;\n}\n\n.bottom-container {\n    width: 100vw;\n    display: flex;\n    gap: 2rem;\n    justify-content: center;\n    padding: 1rem;\n    align-items: center;\n    margin-top: 2.5rem;\n    animation: fadeIn 500ms ease-in;\n}\n\n.bottom-container .img-container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 200px;\n    height: auto;\n    text-align: center;\n}\n\n.bottom-container .img-container img {\n    width: 100px;\n    height: 100px;\n    border-radius: 50px;\n    border: 2px solid #21524e;\n    object-fit: cover;\n    cursor: pointer;\n    transition: transform 200ms ease-in;\n}\n\n.bottom-container .img-container img:hover {\n    transform: scale(1.1);\n}\n\n.img-container a {\n    padding: .5rem;\n    font-size: 1.2rem;\n    color: whitesmoke;\n    font-weight: bold;\n}\n\n/*/////////////////////*/\n/* home page media query */\n/*/////////////////////*/\n\n@media only screen and (max-width: 900px) {\n    .hero {\n        width: 100vw;\n        flex-direction: column-reverse;\n        text-align: center;\n    }\n\n    .hero .left-container {\n        display: flex; \n        flex-direction: column;\n        width: 500px;\n        gap: .5rem;\n        align-items: center;\n        padding: 1.3rem;\n        font-size: 1.2rem;\n    }\n\n    .right-container div {\n        width: 0;\n        height: 0;\n        border: none;\n    }\n    \n    .right-container img {\n        width: 300px;\n        height: 300px;\n        transform: rotate(0);\n        border: .5rem solid #21524e;\n        border-radius: 50%;\n        object-fit: cover;\n    }\n\n    .bottom-container {\n        width: 100vw;\n        flex-wrap: wrap;\n        gap: 0;\n        justify-content: center;\n        padding: .7rem;\n        margin-top: 1rem;\n    }\n}\n\n/*****************/\n/* Menu styling */\n/***************/\n\n.menu-container {\n    display: grid;\n    text-align: center;\n    grid-template-columns: repeat(2, 500px);\n    place-content: center center;\n    place-items: center center;\n    gap: 1.5rem;\n    animation: fadeIn 500ms ease-in;\n}\n\n.menu-container .menu-header {\n    grid-column: 1/3;\n    display: flex;\n    align-items: center;\n    padding: 1rem;\n}\n\n.menu-header h1 {\n    font-size: 3rem;\n    border-bottom: 3px solid #21524e;\n}\n\n.menu-container a {\n    background-color: rgba(245,245,245, 0.7);\n    border: 2px solid #21524e;\n    color: #317b76;\n    width: 400px;\n    padding: 1rem;\n    transition: transform 200ms ease-in;\n    transition: background-color 500ms linear;\n}\n\n.menu-container img {\n    width: 300px;\n    height: 300px;\n    object-fit: cover;\n    border: 1px solid #317b76;\n} \n\n.menu-container a:hover {\n    transform: scale(1.05);\n    background-color: rgba(33, 82, 78, 0.9);\n    color: whitesmoke;\n}\n\n.menu-container .text-container {\n    margin-top: 1rem;\n}\n\n.menu-container p {\n    font-size: 1.3rem;\n    font-weight: bold;\n}\n\n@media only screen and (max-width: 900px) {\n    .menu-container {\n        width: 100vw;\n        padding: 2rem;\n        grid-template-columns: 1fr;\n        gap: 1rem;\n    }\n\n    .menu-container .menu-header {\n        grid-column: 1/2;\n    }\n    \n    .menu-header h1 {\n        font-size: 2.4rem;\n        border-bottom: 2px solid whitesmoke;\n    }\n    \n    .menu-container a {\n        width: 400px;\n        padding: 0;\n        transition: transform 200ms ease-in;\n        transition: background-color 500ms linear;\n    }\n    \n    .menu-container img {\n        width: 100%;\n    } \n\n    .menu-container a:hover {\n        transform: scale(1);\n    }\n\n    .menu-container .text-container {\n        margin: 0;\n        padding: .5rem;\n    }\n\n    .menu-container p {\n        font-size: 1rem;\n        padding: .1rem;\n    }\n\n}\n\n/* About section styling */\n\n.about-container {\n    display: flex;\n    width: 1000px;\n    margin: 0 auto;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    background-color: rgba(33, 82, 78, 0.7);\n    padding: 2rem;\n    animation: fadeIn 500ms ease-in;\n}\n\n.about-header {\n    text-align: center;\n    width: 700px;\n    padding: 1rem;\n}\n\n.about-header h1 {\n    font-size: 2.1rem;\n}\n\n.about-header p {\n    text-align: left;\n    padding: 1rem;\n}\n\n.about-body {\n    display: flex;\n    flex-direction: column;\n    text-align: center;\n}\n\n.about-body h1 {\n    font-size: 2.1rem;\n    padding: 1.5rem;\n}\n\n\n.about-body .contact {\n    display: grid;\n    gap: 1rem;\n    grid-template-columns: 300px 600px;\n}\n\n.contact .contact-info {\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n    color: inherit;\n    text-align: left;\n    padding: 1rem;\n}\n\n.form-container p {\n    text-align: left;\n    padding: 1rem;\n}\n\n.contact #form {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 0.7rem;\n}\n\n#form input {\n    width: 100%;\n    height: 2rem;\n    border: none;\n    background-color: transparent;\n    border-bottom: 2px solid whitesmoke;\n    font-size: 1.1rem;\n    padding: 1rem;\n    color: whitesmoke;\n}\n\n#form textarea {\n    width: 100%;\n    height: 6rem;\n    padding: .5rem;\n}\n\n#form button {\n    width: 100%;\n    padding: .6rem;\n    font-size: 1.2rem;\n    font-weight: bold;\n    border: none;\n    cursor: pointer;\n    background-color: whitesmoke;\n    color: #21524e;\n}\n\n#form input:focus,\n#form textarea:focus,\n#form button:focus {\n    outline: 1px solid #21524e;\n}\n\n#form button:hover {\n    background-color: #21524e;\n    color: whitesmoke;\n}\n\n.contact .map {\n    grid-column: 2/3;\n    grid-row: 1/3;\n}\n\n/******************************/\n/* About section media query */\n/******************************/\n\n@media only screen and (max-width: 900px) {\n    .main-content {\n        padding: 0;\n    }\n\n    .about-container {\n        width: 100vw;\n        background-color: transparent;\n    }\n    \n    .about-header {\n        width: 100vw;\n    }\n    \n    .about-header h1 {\n        font-size: 2.1rem;\n    }\n    \n    .about-header p {\n        text-align: center;\n        line-height: 1.3rem;\n    }\n\n    .about-body .contact {\n        grid-template-columns: 1fr;\n    }\n    \n    .contact .contact-info {\n        font-weight: bold;\n        text-align: center;\n        padding: 1rem;\n    }\n    \n    .form-container p {\n        font-weight: bold;\n        text-align: center;\n        padding: 1rem;\n    }\n\n    #form input {\n        width: 20rem;\n    }\n    \n    #form textarea {\n        width: 20rem;\n        height: 10rem;\n    }\n    \n    #form button {\n        width: 20rem;\n    }\n\n    .contact .map {\n        margin-top: 1rem;\n        grid-column: 1/2;\n        grid-row: 3/4;\n    }\n}\n\n@keyframes fadeIn {\n    0% {\n        opacity: 0;\n    }\n    100% {\n        opacity: 1;\n    }\n}"],"sourceRoot":""}]);
